import { DateFormat, Trans } from '@lingui/react'
import { useXFieldState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { SelectButtonThemed } from '../../common/Buttons'
import { useOrderModel } from '../../hooks/useOrderModel'
import { OrderFormValues } from './OrderFormSetup'

const StyledOrderTimeButtons = styled.div`
  margin: 0 0 1.4rem 0;
  button {
    position: relative;
    padding-top: 0.3rem;
    margin-bottom: 0.2rem;
    height: 2.8rem;
  }
  button:first-of-type {
    margin-right: 0.8rem;
  }
`

export const OrderTimeButtons: React.FC = () => {
  const orderModel = useOrderModel()

  const field = useXFieldState<OrderFormValues['deliverAt']>('deliverAt')

  return useObserver(() => {
    if (!orderModel.delayedOrderEnabled) {
      return null
    }
    const isDelayed = field.value !== null
    return (
      <StyledOrderTimeButtons>
        <SelectButtonThemed
          data-testid="orderTimeButtonNow"
          selected={!isDelayed}
          disabled={orderModel.disableInteraction}
          onClick={() => field.setValue(null)}
        >
          <Trans>When possible</Trans>
        </SelectButtonThemed>
        <SelectButtonThemed
          data-testid="orderTimeButtonLater"
          selected={isDelayed}
          disabled={orderModel.disableInteraction}
          onClick={orderModel.openTimePicker}
        >
          {isDelayed ? (
            <DateFormat
              value={field.value}
              format={{ weekday: 'long', hour: 'numeric', minute: 'numeric' }}
            />
          ) : (
            <Trans>Choose time</Trans>
          )}
        </SelectButtonThemed>
      </StyledOrderTimeButtons>
    )
  })
}
